<template>
  <div class="chart-mask">
    <div class="chart">
      <div class="title">Answer Result</div>
      <ul class="bars" :class="{more: list.length > 3}">
        <li v-for="(item, index) in list" :class="{selected: item.selected, correct: item.correct}">
          <div :data-height="item.val / maxValue * 100" class="bar" :style='{height:item.val / maxValue * 100 +"%"}'>
            <div class="per">{{item.val}}</div>
          </div>
          <span class="name">{{item.name}}</span>
        </li>
      </ul>
      <a href="#none" class="close-btn" @click="onClose" :style="{background: 'url(' + chartClose + ') no-repeat 0 0'}"></a>
    </div>
  </div>
</template>
<script>
  export default {
    name: 'vueSimpleChart',
    data() {
      return {
        maxValue: 0,
        chartClose: require('./assets/button_close.svg')
      }
    },
    props: ['list'],
    methods: {
      updateMaxValue() {
        for (var i in this.list) {
          if (this.list[i].val > this.maxValue) {
            this.maxValue = this.list[i].val
          }
        }
        this.maxValue = Math.ceil((this.maxValue / 10) * 1.1) * 10
      },
      onClose() {
        this.$emit('close_me')
      }
    },
    mounted() {
      this.updateMaxValue()
    },
    watch: {
      list() {
        this.updateMaxValue()
      }
    }
  }
</script>
<style lang='less' scoped>
  .chart-mask {
    background: rgba(0, 0, 0, 0.7);
    position: fixed;
    z-index: 100;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
  }

  .title {
    font-size: 30px;
    font-weight: 600;
    text-align: center;
    color: #cae0ff;
    width: 100%;
  }

  .chart {
    position: absolute;
    left: 36px;
    top: 90px;
    width: 900px;
    height: 520px;
    z-index: 5;
    font-size: 28px;
    color: #FFF;
  }

  .chart .close-btn {
    width: 50px;
    height: 50px;
    position: absolute;
    right: 0px;
    top: 0px;
    background: no-repeat 0 0;
    cursor: pointer;
    pointer-events: auto;
  }

  .chart .bars {
    text-align: center;
    height: 220px;
    padding: 0;
    margin-top: 160px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.7);

    &.more li {
      width: 60px;

      .bar {
        width: 30px;
        left: 15px;
      }

      .per {
        width: 60px;
      }
    }
  }

  .chart .bars li {
    display: inline-block;
    width: 120px;
    height: 100%;
    text-align: center;
    position: relative;
    list-style: none;

    &.selected .name:before {
      width: 36px;
      height: 36px;
      border: solid 6px #448fd3;
      position: absolute;
      margin-left: -24px;
      margin-top: -25px;
      left: 50%;
      top: 50%;
      content: '';
      border-radius: 50%;
    }

    &.correct {
      .bar {
        background-color: #9cd344;
      }

      .name:after {
        width: 40px;
        height: 40px;
        position: absolute;
        left: 50%;
        top: 50%;
        margin-left: -17px;
        margin-top: 5px;
        content: '';
        background: url(data:image/png;base64,);
      }
    }
  }

  .chart .bars li .bar {
    width: 60px;
    border-radius: 6px 6px 0 0;
    background: #cae0ff;
    position: absolute;
    bottom: 0;
    left: 30px;
    transition: height 1s;
  }

  .chart .bars li .per {
    position: absolute;
    width: 120px;
    text-align: center;
    top: -28px;
    font-size: 20px;
    left: -50%;
  }

  .chart .bars li span {
    width: 100%;
    position: absolute;
    bottom: -2em;
    left: 0;
    font-size: 30px;
    text-align: center;
  }
</style>



